import React, { Component } from 'react'
import axios from 'axios';
import Joke from './Joke';
import "./JokeList.css";
import { v4 as uuidv4 } from 'uuid';

const URL = "https://icanhazdadjoke.com/";

class JokeList extends Component {
    static defaultProps = {
        numJokesToGet: 10
    };
    constructor(props) {
        super(props);
        this.state = {
            jokes: []
        }
    }
    async componentDidMount() {
        let jokesArr = [];
        while (jokesArr.length < this.props.numJokesToGet) {
            let res = await axios.get(URL, {
                headers: { Accept: "application/json" }
            });
            jokesArr.push({ id: uuidv4(), joke: res.data.joke, votes: 0 })
        }

        this.setState({ jokes: jokesArr })
    }
    handleVote(id, delta) {
        this.setState(st => ({
            jokes: st.jokes.map(j =>
                j.id === id ? { ...j, votes: j.votes + delta } : j
            )
        }))
    }
    render() {
        let jokesList = this.state.jokes.map(j => (
            <Joke
                joke={j.joke}
                votes={j.votes}
                key={j.id}
                upVote={() => this.handleVote(j.id, 1)}
                downVote={() => this.handleVote(j.id, -1)}
            />
        ))
        return (
            <div className="JokeList">
                <div className="JokeList-sidebar">
                    <h1 className="JokeList-title">
                        <span>Dad</span> Jokes
                    </h1>
                    <img src="https://assets.dryicons.com/uploads/icon/svg/8927/0eb14c71-38f2-433a-bfc8-23d9c99b3647.svg" alt="emoji" />
                    <button>New Jokes</button>
                </div>
                <div className="JokeList-jokes">
                    {jokesList}
                </div>
            </div>
        )
    }
}
export default JokeList;